import {
  IAlterarSenhaView,
  IAlterarSenhaController,
} from "../../contracts/Perfil/AlterarSenhaContract";
import { Storage } from "../../configuration/storage";
import axios from "../../plugins/axios";

export class AlterarSenhaController implements IAlterarSenhaController {
  _view: IAlterarSenhaView;

  constructor(view: IAlterarSenhaView) {
    this._view = view;
  }

  salvar(passNow: String, passNew: String, passNewConfirm: string): void {
    this._view.loading(true);
    axios
      .post("Colaboradores/AlterarSenha", {
        CodColaborador: JSON.parse(Storage.get("Colaborador")!!).codigo,
        PassNow: btoa(passNow.toString()),
        PassNew: btoa(passNew.toString()),
        PassNewConfirm: btoa(passNewConfirm.toString()),
      })
      .then((Response: any) => {
        this._view.loading(false);
        if (Response.data.sucesso) {
          this._view.showMsg("Salvo com sucesso.", 1);
        } else {
          this._view.showMsg(Response.data.message, 0);
        }
      })
      .catch((e: any) => {
        this._view.loading(false);
        this._view.showMsg("Náo foi possível carregar as informações.", 1);
        console.log(e);
      });
  }
}
