























































































import Vue from "vue";
import KLoading from "../../components/KLoading.vue";
import KDialog from "../../components/KDialog.vue";
import KDataList from "../../components/KDataList.vue";

import { Display } from "../../configuration/display";

import { Header, HeaderIcon, HeaderAction } from "../../models/header";
import { Headers } from "../../models/headers";

import {
  IAlterarSenhaView,
  IAlterarSenhaController
} from "../../contracts/Perfil/AlterarSenhaContract";

import { AlterarSenhaController } from "../../controllers/Perfil/AlterarSenhaController";

import Component from "vue-class-component";

@Component({
  components: {
    KLoading,
    KDialog,
    KDataList
  }
})
export default class UserServiceLayer extends Vue implements IAlterarSenhaView {
  _controller?: IAlterarSenhaController;

  dialogLoading = false;
  dialogMessage = false;
  textError = "";
  titleMsg = "Aviso";

  visiblePassPassNow = false;
  visiblePassPassNew = false;
  visiblePassPassNewConfirm = false;

  sairMessage = false;

  user = {
    passNow: "",
    passNew: "",
    passNewConfirm: ""
  };

  loading(visible: boolean): void {
    this.dialogLoading = visible;
  }
  showMsg(msg: string, modo: number): void {
    this.textError = msg;
    this.dialogMessage = true;
    this.sairMessage = modo == 1;
  }

  fecharDialogMessage() {
    this.dialogMessage = false;
    if (this.sairMessage) {
      this.$router.push("/");
    }
  }

  isMobile = false;

  save() {
    this._controller!!.salvar(
      this.user.passNow,
      this.user.passNew,
      this.user.passNewConfirm
    );
  }

  created() {
    this.isMobile = Display.isMobile();
    this._controller = new AlterarSenhaController(this);
  }
}
